import React from 'react';
import ReactDOM from 'react-dom';
import SignatureCanvas from 'react-signature-canvas';

class UDSignature extends React.Component {
  // state = { trimmedDataURL: null }
  constructor(props) {
    super(props);
    this.sigCanvas = React.createRef();
    this.state = {
      trimmedDataURL: null,
      hidden: false
    }
  }

  handleChange = trimmedDataURL => {
    this.setState({ trimmedDataURL });
  };

  componentWillMount() {
    this.pubSubToken = PubSub.subscribe(this.props.id, this.onIncomingEvent.bind(this));
  }

  componentWillUnmount() {
    PubSub.unsubscribe(this.pubSubToken);
  }

  onIncomingEvent(eventName, event) {
    if (event.type === "requestState") {
      var data = {
        attributes: {
          trimmedDataURL: this.state.trimmedDataURL,
          hidden: this.state.hidden,
        }
      }
      UniversalDashboard.post(`/api/internal/component/element/sessionState/${event.requestId}`, data);
    }
    else if (event.type === "setState") {
      this.setState(event.state.attributes);
    }
    else if (event.type === "clearElement") {
      this.setState({
        trimmedDataURL: null
      });
    }
    else if (event.type === "removeElement") {
      this.setState({
        hidden: true
      });
    }
  }
  clearCanvas = () => {
    this.sigCanvas.current.clear();
    this.setState({
      trimmedDataURL: null
    });
  }
  saveSignature = () => {
    // console.log(this.sigCanvas.current.toDataURL());
    this.setState({
      trimmedDataURL: this.sigCanvas.current.toDataURL('image/png')
    });
  }
  render() {
    // let { trimmedDataURL } = this.state
    const { trimmedDataURL } = this.state;
    if (this.state.hidden) {
      return null;
    }
    return (
      <div>
        <div style={{ width: this.props.width, height: this.props.height, border: "1px dotted black" }}>
          <SignatureCanvas ref={this.sigCanvas} penColor={this.props.penColor} clearOnResize="false" backgroundColor={this.props.backgroundColor} />
        </div>
        <button onClick={this.saveSignature}>Save</button>
        <button onClick={this.clearCanvas}>Clear</button>
      </div>
    )

  }
}

export default UDSignature
